.sidebarChat {
  display: flex;
  padding: 20px;
  cursor: pointer;
  border-bottom: 1px solid #f6f6f6;
}

.sidebarChat:hover {
  background-color: #ebebeb;
}

.sidebarChat:hover .sidebarChat__icon {
  color: rgb(42, 228, 42);
}

.sidbarChat__info h2 {
  font-size: 16px;
  margin-bottom: 8px;
}

.sidbarChat__info {
  margin-left: 15px;
}
